<template>
    <div
      aria-disabled="false"
    > <div class="wssearch--filter-header"  >
            <label>{{labelname}}</label>
          </div>
      <div
       class="wssearch--search-wrapper"
      >
      <input
        type="text"
        :placeholder="$i18n( 'search' )"
        class="wssearch--facetsearch-input"
        @keyup.enter="search"
        v-model="termtyped"
        tabindex="0"
        aria-disabled="false"
      />
      <span
        class="wssearch--search-icon"
      >
      </span>
      <span
        v-if="termtyped"
        class="wssearch--search-clear"
        tabindex="-1"
        aria-label="Clear"
        role="button"
        @click="clearTerm"
      >
      </span>
      </div>
        <span
        v-if="false"
        class="wssearch--search-button"
        aria-label="Search"
        @click="search"
        role="button"
         tabindex="1"
         aria-disabled="false"
      >
        {{$i18n( 'search' )}}
      </span>
      <input type="checkbox" :value="val" :id="setid" @change="clearTerm" style="display:none">
    </div>
</template>

<script>
  export default {
    computed:{
       labelname(){
        return this.label ? this.label : this.name
      },
      val(){
          return {key:this.name, value:this.termtyped , type: "query"};
      },
      setid(){
          return this.name.toLowerCase().replace(/[^a-z0-9]/gmi, '_') +"--"+ this.termtyped.toLowerCase().replace(/[^a-z0-9]/gmi, '_')
      },
      term:{
       set(value){
          this.termclear =  value;
          this.userhastyped = true;
        },
        get(){
           if(this.termclear || this.userhastyped){
             return this.termclear;
           }
           return this.$store.state.term
        }
       
      }
    },
    props:{
      name:String,
      label:String
    },
    data(){
      return {
        termclear:"",
        userhastyped:false,
        termtyped:""
      }
    },
    methods:{
        search(){
        
          // this.$store.commit('SET_TERM', this.termclear);
          var that = this 
          console.log({key:this.name, value:this.termtyped , type: "query"})
            var val =  this.$store.state.selected;
             var ns = val.filter(function(el){
              if(el.key != that.name){
                  return el;
              }
           })
           if(this.termtyped){
            ns.push({key:this.name, value:this.termtyped , type: "query"});
           }
          this.$store.commit('SET_SELECTED', ns)

          
      
           
       

        },
        clearTerm(){
           this.termtyped =  "";
           var that = this;
           var val = this.$store.state.selected;
           var ns = val.filter(function(el){
              if(el.key != that.name){
                  return el;
              }
           })
          this.$store.commit('SET_SELECTED', ns)
        }
      
    }
  }
</script>

